
import { Component, Prop, Vue, Model } from 'vue-property-decorator';
import {
  State,
  Action,
  Mutation,
} from 'vuex-class';

@Component
export default class Item extends Vue {
  @Prop({required: true})
  private text!: string;

  @Prop({required: true})
  private name!: string;

  @Prop({required: true})
  private valueStr!: string;

  @State('navState')
  private navState!: string;

  @Mutation('setNavState')
  private setNavState!: (str: string) => {};

  public render() {
    return (
      <label class='item_container'>
        <input
          onChange={() => this.setNavState(this.valueStr)}
          class='hidden_radio'
          type='radio'
          name={this.name}
          value={this.valueStr}
          checked={(this.navState === this.valueStr)}
        />
        <label>{this.text}</label>
      </label>
    );
  }
}
